(function (factory) {
	typeof define === 'function' && define.amd ? define(factory) :
	factory();
}(function () { 'use strict';

	/**
	 * Fade Out method
	 * @param el
	 */
	function fadeOut(el){
		el.style.opacity = 1;

		(function fade() {
			if ((el.style.opacity -= .1) < 0) {
				el.style.display = 'none';
			} else {
				requestAnimationFrame(fade);
			}
		})();
	}



	/**
	 * Fade In method
	 * @param el
	 * @param display
	 */
	function fadeIn(el, display){
		if (el) {
			el.style.opacity = 0;
			el.style.display = display || 'block';
		
			(function fade() {
				let val = parseFloat(el.style.opacity);
				if (!((val += .1) > 1)) {
					el.style.opacity = val;
					requestAnimationFrame(fade);
				}
			})();
		}
	}


	/**
	 * Check if element in viewport
	 * @param el
	 * @param offset - Adjustable offset value when element becomes visible
	 * @returns {boolean}
	 */
	function isInViewport(el, offset = 100) {

		if ( !el ) return false;

		const scroll    = window.scrollY || window.pageYOffset;
		const boundsTop = el.getBoundingClientRect().top + offset + scroll;

		const viewport = {
			top   : scroll,
			bottom: scroll + window.innerHeight,
		};

		const bounds = {
			top   : boundsTop,
			bottom: boundsTop + el.clientHeight,
		};

		return ( bounds.bottom >= viewport.top && bounds.bottom <= viewport.bottom )
			|| ( bounds.top <= viewport.bottom && bounds.top >= viewport.top );

	}

	function hasSomeParentTheClass(element, classname) {
		if (element.classList && [...element.classList].includes(classname)) return element;
	    return element.parentNode && hasSomeParentTheClass(element.parentNode, classname);
	}

	class Popup {
		constructor() {
			this.body = document.querySelector('body');
			this.html = document.querySelector('html');
		}



		/**
		 * Force Close All opened popup window
		 * and clear the traces of an opened popup window
		 */
		forceCloseAllPopup() {
			[...document.querySelectorAll('.popup')].forEach( (item) => {
				fadeOut( item );

				const MAIL_SENT_OK_BOX = item.querySelector('.wpcf7-mail-sent-ok');
				if ( MAIL_SENT_OK_BOX ) {
					MAIL_SENT_OK_BOX.style.display = 'none';
				}

			});

			this.body.classList.remove('popup-opened');
			this.html.classList.remove('popup-opened');
		}



		/**
		 * Open selected popup window
		 * @param popupSelector - css selector of popup that should be opened
		 * @param timeOut - ms
		 */
		openOnePopup(popupSelector = null, timeOut = 1000) {
			this.forceCloseAllPopup();

			setTimeout( () => {
				this.body.classList.add('popup-opened');
				this.html.classList.add('popup-opened');

				fadeIn(document.querySelector(popupSelector));
			}, timeOut);
		}



		/**
		 * Opening popup window
		 */
		openPopup() {

			this.body.addEventListener('click', (event) => {

				const el_with_class = hasSomeParentTheClass(event.target, 'js-open-popup-activator');

				if (!el_with_class) {
					return false;
				}

				event.preventDefault();

				let el_href = ( el_with_class.nodeName === 'A' )
									? el_with_class.getAttribute('href')
									: el_with_class.dataset.href;

				const POPUP_ELEMENT    = document.querySelector(el_href);
				//const POPUP_FORM_INPUT = POPUP_ELEMENT.querySelector('form input[type="text"]');

				this.body.classList.add('popup-opened');
				this.html.classList.add('popup-opened');

				fadeIn( POPUP_ELEMENT );

				//POPUP_FORM_INPUT && POPUP_FORM_INPUT.focus();
			});

		}



		/**
		 * Closing popup window
		 */
		closePopup() {
			this.body.addEventListener('click', (event) => {

				// Check if user click on close element
				if ( ![...event.target.classList].includes('js-popup-close') ) {
					return false;
				}

				event.preventDefault();
				this.forceCloseAllPopup();

			});


			// Checking ESC button for closing opened popup window
			document.addEventListener('keydown', (event) => {
				if( event.keyCode === 27 ) {
					this.forceCloseAllPopup();
				}
			});

		}


		init() {
			this.openPopup();
			this.closePopup();
		}
	}

	function menuClassHandle() {
	    const SITE_HEADER = document.querySelector('#site-header');

	    if (!SITE_HEADER) {
	        return;
	    }

	    // check for pages wich started not from top o the page
	    if ( window.scrollY>1 ) {
	        SITE_HEADER.classList.add('scrolled');
	    } else {
	        SITE_HEADER.classList.remove('scrolled');
	    }

	    window.addEventListener('scroll', () => {
	        if ( window.scrollY>1 ) {
	            SITE_HEADER.classList.add('scrolled');
	        } else {
	            SITE_HEADER.classList.remove('scrolled');
	        }
	    });
	}

	function burger() {
	  // BURGER
	  const $menuBtn = $('.menu-btn');
	  const $siteHeader = $('.site-header');
	  const $body = $('body');
	  const $html = $('html');

	  $menuBtn.on('click', () => {
	    if($menuBtn.hasClass('open')) {
	      $menuBtn.removeClass('open');
	      $siteHeader.removeClass('open');
	      $body.removeClass('menu-opened');
	      $html.removeClass('menu-opened');
	    } else {
	      $menuBtn.addClass('open');
	      $siteHeader.addClass('open');
	      $body.addClass('menu-opened');
	      $html.addClass('menu-opened');
	    }
	  });
	}

	function clickOnHash($) {
	    $('a').click(function(event) {
	        const link = $(event.target).attr('href');
	        const hash = link.substr(link.indexOf('#'));
	        const $hash = $(hash);

	        if ($hash[0]) {
	            const offset = $hash.offset().top - $('#site-header').height();

				$('html, body').animate({
					scrollTop: offset,
				}, 1500, 'swing');
			}
	    });
	}

	function scrollToURLHash($) {
	    if(window.location.hash) {
			const $hash = $(window.location.hash);

			if ($hash[0]) {
	            const offset = $hash.offset().top - $('#site-header').height();

				$('html, body').animate({
					scrollTop: offset,
				}, 1500, 'swing');
			}
		}
	}

	function initPopups() {
	    // Init popups Windows
	    const popup_windows = new Popup();
	    popup_windows.init();
	    window.popup_windows = popup_windows;
	}

	function inViewportChecking($) {
	    const STAGES_ITEMS             = document.querySelectorAll(
	        '.stages-holder .stage'
	    );

	    $(window).on('scroll', () => {
	        // Adding scroll animation to Stage items
	        ( STAGES_ITEMS ) && [...STAGES_ITEMS].forEach( (item) => {
	            if ( isInViewport(item, 0) ) {
	                item.classList.add('in-viewport');
	            } else {
	                item.classList.remove('in-viewport');
	            }
	        });
	    });
	}

	function cookieReminderInit() {
	    const CLOSE_COOKIE_REMINDER    = document.querySelectorAll('.js-close-reminder');
	    const COOKIE_POLICY_BOX        = document.querySelector('.js-cookie-policy-box');

	    const cookie_policy_check = Cookies.get('cookie_policy');

	    if ( cookie_policy_check !== 'viewed' ) {
	        setTimeout( () => {
	            (COOKIE_POLICY_BOX) && COOKIE_POLICY_BOX.classList.add('opened');
	        }, 2000 );
	    }

	    (CLOSE_COOKIE_REMINDER) && CLOSE_COOKIE_REMINDER.forEach( item => {

	        item.addEventListener('click', (event) => {
	            event.preventDefault();

	            (COOKIE_POLICY_BOX) && COOKIE_POLICY_BOX.classList.remove('opened');
	            Cookies.set('cookie_policy', 'viewed', {expires: 40});
	        });

	    });
	}

	function parallax($) {
	    function parallaxEvent() {
	        if (!window.matchMedia('(max-width: 1439px)').matches) {
	            const windowHeight = $(window).height();
	            doParallax($('.circle-1'), windowHeight, 'top');
	            doParallax($('.circle-2'), windowHeight, 'bottom');
	            doParallax($('.circle-3'), windowHeight, 'top');
	            doParallax($('.circle-4'), windowHeight, 'bottom');
	            doParallax($('.circle-5'), windowHeight, 'top');
	        }
	    }

	    parallaxEvent();
	    $(window).scroll(parallaxEvent);
	    $(window).resize(parallaxEvent);
	}

	function doParallax($element, windowHeight, position) {
	    const elementTop = $element.offset().top;
	    const circleTop = window.scrollY - elementTop + windowHeight;

	    if (circleTop > 0 && circleTop < windowHeight) {
	        const newCircleTop = circleTop / 2;
	        if ('top' === position) {
	            $element.css('top', newCircleTop+'px');
	        } else {
	            $element.css('bottom', '-'+(newCircleTop / 1.5)+'px');
	        }
	        
	    }
	}

	function termsPageNavigation($) {
	    const SITE_HEADER = document.querySelector('#site-header');
	    const TERMS_NAVIGATION = document.querySelector('.js-terms-page-navigation');
	    const TERMS_ANCHOR = (TERMS_NAVIGATION) && TERMS_NAVIGATION.querySelectorAll('li > a');
	    const TERMS_SECTIONS = document.querySelectorAll('.terms-content h4');
	    const TERMS_MENU_OPENER = document.querySelector('.js-terms-menu-opener');

	    // terms page sticky for mobile
	    function termsSticky() {
	        const navWrapper = TERMS_NAVIGATION.closest('.terms-navigation-wrapper');

	        if (window.matchMedia( '(min-width: 768px)' ).matches || window.scrollY < 24) {
	            navWrapper.removeAttribute('style');
	        } else  {
	            navWrapper.style.transform = `translateY(${window.scrollY - 24}px)`;
	        }
	    }

	    // terms set active menuitem
	    function termsActive() {
	        let menuMargin = SITE_HEADER.clientHeight + 70;

	        if (!window.matchMedia( '(min-width: 1200px)' ).matches) {
	            menuMargin -= 110;
	        }

	        [...TERMS_SECTIONS].forEach( section => {

	            const id = section.getAttribute('id');

	            const scrollToTop = section.offsetTop;

	            if ( scrollToTop < menuMargin + window.scrollY ) {

	                [...TERMS_ANCHOR].forEach( btn => {
	                    btn.classList.remove('active');
	                });

	                TERMS_NAVIGATION.querySelector(`[href="#${id}"]`).classList.add('active');
	            }
	        });
	    }
	    
	    // terms page check
	    if (TERMS_NAVIGATION) {
	        setTimeout(termsSticky, 0);
	        termsActive();

	        $(window).on('scroll', function() {
	            termsActive(); // TODO: is this function realy neede here?
	            termsSticky();
	        });

	        $(window).on('resize', function() {
	            setTimeout(termsSticky, 0);
	            setTimeout(termsActive, 0);
	        });
	    }


	    // ===========================================
	    // Terms page navigation

	    (TERMS_NAVIGATION) && [...TERMS_ANCHOR].forEach( btn => {
	        btn.addEventListener('click', (event) => {
	            event.preventDefault();

	            const id = event.target.getAttribute('href');
	            const section = document.querySelector(id);

	            let scrollToTop = section.offsetTop - SITE_HEADER.clientHeight;

	            if (window.matchMedia( '(min-width: 1651px)' ).matches && !SITE_HEADER.classList.contains('scrolled')) {
	                scrollToTop += 20;
	            } else if (!window.matchMedia( '(min-width: 1200px)' ).matches) {
	                scrollToTop += 50;
	            } else {
	                scrollToTop -= 20;
	            }


	            (section) && window.scroll({
	                'behavior': 'smooth',
	                'left'    : 0,
	                'top'     : scrollToTop,
	            });
	        });


	    });

	    // terms open menu for tablet
	    (TERMS_MENU_OPENER) && TERMS_MENU_OPENER.addEventListener('click', () => {
	        const mainSection = TERMS_MENU_OPENER.closest('.section');

	        if (mainSection.classList.contains('active')) {
	            mainSection.classList.remove('active');
	        } else {
	            mainSection.classList.add('active');
	        }
	    });
	}

	function ticksListAccordion($) {
	    const $list = $('.ticks-list');

	    if ($list[0]) {
	        $list.find('.tick.expandable').on('click', function() {
	            const $tick = $(this);

	            $list.find('.tick').removeClass('active');

	            $tick.addClass('active');
	        });

	    }
	}

	(function($){

		$(function() {
			menuClassHandle();
			burger();
			inViewportChecking($);
			ticksListAccordion($);
			scrollToURLHash($);
			clickOnHash($);
			initPopups();
			termsPageNavigation($);
			cookieReminderInit();
			parallax($);
		});

	})(jQuery);

}));
